export default {
    data () {
        return {
            name:'',
            coloe:"",
            ids:'',
            days:'',
            rate:''
        }
    },
    onLoad(option) {
         this.ids = option.ids
         this.days = option.day
         this.rate = option.rate
    },
    methods:{
        end:function(e){
            this.name = e.detail.value
            console.log(this.name)
        },
        ennd:function(e){
            this.coloe = e.detail.value
            console.log(this.coloe)
        },
       step:function() {
           var that = this
           var name = that.name
           var cool = that.coloe
           console.log(that.ids)
            if(this.name==""){
                uni.showToast({
                    icon:'none',
                    title:'请输入姓名'
                })
                return false;
               }else if (this.coloe == "") {
                   uni.showToast({
                       icon:'none',
                       title:'请输入手机号'
                   })
               }
           var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;	
           if(!myreg.test(this.coloe)){
           	 uni.showToast({
           	 	title:'请输入正确的手机号',
           		icon:'none',
           		mask:true,
           		duration:2000
           	 })
           	  return false
            }
            uni.navigateTo({
                url:'../confirm-order/confirm-order?name=' + name + '&cllo=' + cool + '&ids=' + that.ids + '&day=' + that.days + '&shadrate=' + that.rate
            })
       } 
    }
}
